.movie-tabs {
  box-sizing: border-box;
  height: 100vh;
  background-color: #fff;
  width: 1010px;
  overflow: auto;
  margin: 0 auto;
  padding: 36px;
  padding-top: 0;
}

.movie-tabs::-webkit-scrollbar {
  width: 0;
}

@media (max-width: 1010px) {
  .movie-tabs {
    width: 100%;
    padding: 16px;
    padding-top: 0;
  }
}
